body {
  background-color: #0d0d0d;
  color: #e0e0e0;
  font-family: 'Orbitron', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.game-container {
  text-align: center;
  background: linear-gradient(145deg, #1f1f1f, #333333);
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0 0 15px rgba(0, 255, 255, 0.5);
}

.game-title {
  font-size: 2rem;
  color: #00ffcc;
  text-shadow: 0 0 10px #00ffcc, 0 0 20px #00ffcc;
}

.game-input {
  padding: 10px;
  font-size: 1.2rem;
  border: none;
  background-color: #333333;
  color: #00ffcc;
  margin: 20px 0;
  width: 80%;
  border-radius: 10px;
}

.game-button, .restart-button {
  padding: 10px 20px;
  margin: 10px;
  font-size: 1.2rem;
  background-color: #00ffcc;
  color: #0d0d0d;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 0 0 10px #00ffcc;
  transition: background 0.3s ease;
}

.game-button:hover, .restart-button:hover {
  background-color: #009999;
}

.message {
  font-size: 1.5rem;
  color: #00ffcc;
  text-shadow: 0 0 10px #00ffcc;
} 
.lives {
  font-size: 1.2rem;
  color: #ff4d4d;
  text-shadow: 0 0 10px #ff4d4d;
  margin-top: 10px;
}

